@font-face {
  font-family: "Montserrat-Bold";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff") format("woff"), url("../fonts/Montserrat-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Roboto-Black";
  font-display: swap;
  src: url("../fonts/Roboto-Black.woff") format("woff"), url("../fonts/Roboto-Black.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
}

@font-face {
  font-family: "Roboto-Bold";
  font-display: swap;
  src: url("../fonts/Roboto-Bold.woff") format("woff"), url("../fonts/Roboto-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Roboto-Regular";
  font-display: swap;
  src: url("../fonts/Roboto-Regular.woff") format("woff"), url("../fonts/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

.first-block {
  background: url("../img/ellipses.png") no-repeat center/cover, url("../img/first-block-bg.jpg") no-repeat center/cover;
  padding: 40px 0 calc(110px + (198 - 110) * ((100vw - 320px) / (1276 - 320))) 0;
  position: relative;
  overflow: hidden;
}

.first-block .header {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 50px;
  position: relative;
  z-index: 2;
}

.first-block .header__btn {
  color: #5101A6;
  background: #FFFFFF;
  height: 40px;
  padding: 0 12px;
  font-family: "Roboto-Black";
  justify-self: flex-end;
}

.first-block__img-arrows {
  position: absolute;
  top: 0;
  left: 23%;
  height: 100%;
}

.first-block-content {
  margin-top: calc(55px + (119 - 55) * ((100vw - 320px) / (1276 - 320)));
  position: relative;
  z-index: 2;
}

.first-block-content__title {
  color: #FFFFFF;
  font-size: calc(46px + (48 - 46) * ((100vw - 320px) / (1276 - 320)));
  line-height: calc(54px + (56 - 54) * ((100vw - 320px) / (1276 - 320)));
}

.first-block-content__description {
  color: #FFFFFF;
  font-family: "Roboto-Regular";
  font-size: 20px;
  line-height: 28px;
  max-width: 500px;
  margin-top: 48px;
}

.button-block-wrapper {
  display: flex;
  margin-top: 50px;
}

.button-block-wrapper__btn {
  color: #5101A6;
  background: #FFFFFF;
  font-family: "Roboto-Black";
  height: 48px;
  padding: 0 16px;
}

.button-block-wrapper .play {
  margin-left: 16px;
  width: 48px;
  height: 48px;
  background: #5101A6;
  border-radius: 68px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.second-block {
  position: relative;
  padding: calc(65px + (168 - 65) * ((100vw - 320px) / (1276 - 320))) 0 calc(76px + (96 - 76) * ((100vw - 320px) / (1276 - 320))) 0;
}

.second-block__title {
  color: #200142;
  text-align: center;
  font-size: 40px;
  line-height: 48px;
  padding-bottom: calc(48px + (76 - 48) * ((100vw - 320px) / (1276 - 320)));
}

.second-block__img-ellipse-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 50%;
  top: calc(-100px + (-560 - (-100)) * ((100vw - 320px) / (1276 - 320)));
  border: 2px solid rgba(255, 255, 255, 0.05);
  border-radius: 50%;
  padding: 30px;
}

.second-block__img {
  filter: drop-shadow(24px 24px 80px rgba(56, 11, 103, 0.3));
  max-width: 100%;
}

.second-block-content {
  position: relative;
}

.second-block-content .tabs-nav-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.second-block-content .tabs-nav-wrapper .tabs-nav {
  display: flex;
  font-family: "Roboto-Bold";
}

.second-block-content .tabs-nav-wrapper .tabs-nav__item {
  letter-spacing: 0.5px;
  cursor: pointer;
  color: #200142;
}

.second-block-content .tabs-nav-wrapper .tabs-nav__item:last-child {
  margin-left: 48px;
}

.second-block-content .tabs-nav-wrapper .tabs-nav__item.active {
  color: #5101A6;
  position: relative;
}

.second-block-content .tabs-nav-wrapper .tabs-nav__item.active:before {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  background: #5101A6;
  bottom: -10px;
}

.second-block-content .tabs-nav-wrapper .arrows-wrapper {
  width: 112px;
  display: flex;
  justify-content: space-between;
}

.second-block-content .tabs-nav-wrapper .arrows-wrapper .next-slide,
.second-block-content .tabs-nav-wrapper .arrows-wrapper .prev-slide {
  width: 48px;
  height: 48px;
  border: 0;
  font-size: 0;
  background: #F6F6FA;
  border-radius: 6px;
  cursor: pointer;
  padding: 12px;
  z-index: 1;
}

.second-block-content .tabs-nav-wrapper .arrows-wrapper .next-slide:focus,
.second-block-content .tabs-nav-wrapper .arrows-wrapper .prev-slide:focus {
  outline: none;
}

.second-block-content .tabs-content {
  margin-top: 44px;
}

.second-block-content .tabs-content__item {
  display: flex !important;
  height: 0px;
  overflow: hidden;
}

.second-block-content .tabs-content__item.active {
  height: auto;
}

.second-block-content .tabs-content__item .card {
  display: flex;
  flex-direction: column;
  flex-basis: 256px;
  cursor: pointer;
}

.second-block-content .tabs-content__item .card__img {
  border-radius: 8px;
}

.second-block-content .tabs-content__item .card__title {
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: #5101A6;
  font-family: "Montserrat-Bold";
  font-size: 14px;
  line-height: 24px;
  margin: 24px 0 8px 0;
}

.second-block-content .tabs-content__item .card__description {
  font-size: 13px;
  line-height: 20px;
  color: #000000;
  font-family: "Roboto-Regular";
}

.third-block {
  padding: 72px 0 192px 0;
  position: relative;
  overflow: hidden;
  background: url("../img/ellipses-bg.png") center center no-repeat, linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), #5101A6;
}

.third-block .img-wrapper {
  height: 100%;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  top: 0;
  opacity: 0.5;
  z-index: 1;
}

.third-block__title {
  text-align: center;
  font-size: 32px;
  line-height: 40px;
  color: white;
  z-index: 2;
  position: relative;
}

.third-block__subtitle {
  text-align: center;
  max-width: 576px;
  margin: 32px auto 48px auto;
  font-size: 20px;
  line-height: 28px;
  color: white;
  font-family: "Roboto-Regular";
  z-index: 2;
  position: relative;
}

.third-block .form {
  display: flex;
  justify-content: space-between;
  width: 100%;
  z-index: 2;
  position: relative;
}

.third-block .form fieldset {
  border: 1px solid #BB6BD9;
  box-sizing: border-box;
  border-radius: 6px;
  width: 256px;
  height: 79px;
  transform: translateY(-7px);
}

.third-block .form fieldset legend {
  font-size: 12px;
  font-family: "Roboto-Regular";
  color: #FFFFFF;
  padding: 0 14px;
  margin-left: 3%;
  text-align: left;
}

.third-block .form input {
  background-color: transparent;
  border: 0;
  box-sizing: border-box;
  border-radius: 6px;
  padding: 24px;
  width: 100%;
  height: 100%;
  color: #BB6BD9;
  font-size: 16px;
  font-family: "Roboto-Regular";
}

.third-block .form input:focus::placeholder {
  opacity: 0;
  visibility: hidden;
}

.third-block .form input.email {
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  width: 256px;
  height: 72px;
}

.third-block .form button {
  text-transform: uppercase;
  background: #5101A6;
  font-family: "Roboto-Bold";
  padding: 0 81.5px;
  color: white;
  width: 256px;
  height: 72px;
  font-size: 16px;
}

.third-block svg {
  z-index: 2;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}

input::placeholder {
  color: #BB6BD9;
  font-size: 16px;
  font-family: "Roboto-Regular";
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
  background: #0B0B0B;
  z-index: 1;
  height: 80px;
  mix-blend-mode: multiply;
  opacity: 0.4;
}

.slick-slider {
  width: 100%;
}

.slick-track {
  display: flex;
  justify-content: space-between;
}

.slick-dots {
  display: flex;
  justify-content: center;
  margin-top: 48px;
}

.slick-dots li {
  list-style-type: none;
  display: flex;
}

.slick-dots li button {
  font-size: 0;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  border: 0;
  background: #E0E0E0;
  margin: 0 8px;
  cursor: pointer;
}

.slick-dots li button:focus,
.slick-dots li button:active {
  outline: none;
}

.slick-dots li.slick-active button {
  background-color: #5101A6;
}

#tab1,
#tab2 {
  position: relative;
}

* {
  padding: 0;
  margin: 0;
}

button {
  cursor: pointer;
}

button:focus {
  outline: none;
}

.container {
  max-width: 1276px;
  padding: 0 30px;
  margin: 0 auto;
}

a,
input {
  text-decoration: none;
}

a:focus,
input:focus {
  outline: none;
}

.btn {
  border-radius: 6px;
  border: 0;
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
  font-size: 12px;
}

.title {
  font-family: "Montserrat-Bold";
}

@media all and (min-width: 1276px) {
  .second-block__img-ellipse-wrapper {
    top: -60%;
  }
}

@media all and (max-width: 1200px) {
  .third-block .form {
    flex-direction: column;
    align-items: center;
  }

  .third-block .form fieldset,
  .third-block .form .email,
  .third-block .form button {
    margin-top: 15px;
  }
}

@media all and (max-width: 600px) {
  .second-block-content .tabs-nav-wrapper .tabs-nav {
    justify-content: center;
    width: 100%;
  }

  .second-block-content .tabs-nav-wrapper .arrows-wrapper {
    display: none;
  }

  .slick-track {
    justify-content: space-around;
  }
}

@media all and (max-width: 490px) {
  .first-block .header {
    grid-template-columns: 1fr;
  }

  .first-block .header__btn {
    justify-self: flex-start;
    margin-top: 20px;
  }
}